/*___________________CONTACT_ME_____________*/
#contactMe{
  padding: 20px 0 0;
  color: #3d4451;
  position: relative;
}
#contactMe .cardLeaf{
  padding: 40px;
}
#contactMe h3{
  margin-bottom: 25px;
}
#contactMe input[type=text], input[type=number], #contactMe textarea{
  width: 100%;
  background-color: transparent;
  border: none;
  border-bottom: 1px #999 solid;
  margin-bottom: 10px;
  resize: none;
  height: 1.9rem;
}
form>div{
  height: 60px;
}
form{
  padding-top: 5px;
}
input:focus, textarea:focus, button:focus{
  outline: none;
}
.labelUp{
  position: relative;
  font-weight: normal;
  top:-65px;
  font-size:12px;
  color:#fec107;
  text-transform: uppercase;
}
.inputNotEmpty{
  font-weight: normal;
  position:relative;
  top:-65px;
  font-size:12px;
  pointer-events:none;
  -webkit-transition:0.2s ease all;
  transition:0.2s ease all;
  text-transform: uppercase;
}
.inputEmpty{
  color: #3d4451;
  font-size:15px;
  font-weight:lighter;
  position:relative;
  pointer-events:none;
  top: -45px;
  -webkit-transition:0.2s ease all;
  transition:0.2s ease all;
  text-transform: uppercase;
}
.green{
  color:#fec107;
}
.red{
  color: red;
}
.redBar:before, .redBar:after{
  content:'';
  height:3px;
  width:0;
  bottom: 10px;
  position:absolute;
  background: red;
  -webkit-transition:0.2s ease all;
  transition:0.2s ease all;
}
.bar{
  position:relative;
  display:block;
  width:100%;
}
.yellowBar:before, .yellowBar:after{
  content:'';
  height:3px;
  width:0;
  bottom: 10px;
  position:absolute;
  background:#fec107;
  -webkit-transition:0.2s ease all;
  transition:0.2s ease all;
}
.bar:before {
  left:50%;
}
.bar:after {
  right:50%;
}
.mess .bar{
  bottom: 5px;
}
#contactMe input:focus ~ .bar:before, input:focus ~ .bar:after {
  width:50%;
}
#contactMe textarea:focus ~ .bar:before, textarea:focus ~ .bar:after {
  width:50%;
}
#contactMe button{
  width: 150px;
  height: 50px;
  text-transform: uppercase;
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 4px;
  /*margin-top: 20px;*/
}
.buttonShake{
  -webkit-animation: shake 0.5s;
          animation: shake 0.5s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}
@-webkit-keyframes shake {
  0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
  10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
  20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
  30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
  40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
  50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg); }
  60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
  70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
  80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
  90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
  100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}
@keyframes shake {
  0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
  10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
  20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
  30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
  40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
  50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg); }
  60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
  70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
  80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
  90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
  100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}
